@import "../common/mixins";
@import "../common/variable";

.about-text {
  padding-top: r(100);
  padding-bottom: r(100);
  @include max-tablet {
    padding: r(50) 0;
  }
  &__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-gap: r(20);
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    @include max-tablet {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      grid-gap: r(35);
    }
  }
  &__picture {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    max-width: r(687);
    font-size: 0;
    picture {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      font-size: 0;
    }
    img {
      width: 100%;
    }
  }
  &__right {
    width: 100%;
    max-width: r(685);
  }
  &__title {
    color: $colorOrange;
  }
  &__content {
    margin-top: r(35);
  }
  &__action {
    margin-top: r(35);
  }
}

.about-text_reverse {
  .about-text {
    &__wrapper {
      @include min-tablet {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
            -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
      }
    }
  }
}